import {
    HEADER_INDEXATION,
    HEADER_NEXT_INDEXATION,
    HEADER_CURRENCY,
    HEADER_LEASE_START,
    HEADER_LEASE_EXPIRY,
    HEADER_RENT_INCOME_MONTHLY,
    HEADER_ESTIMATED_VOID_PERIOD_MONTHS,
    HEADER_ERV_FOR_NEW_LEASE_MONTH,
    HEADER_NEW_LEASE_TERM_MONTHS,
    HEADER_ESTIMATED_INDEFINITE_PERIOD_END,
    HEADER_GROSS_LETTABLE_AREA_SQM,
    HEADER_TENANT_NAME,
    HEADER_TYPE_OF_AREA,
    HEADER_BREAK_OPTION_DATE,
    HEADER_AGENT_FEE_OF_HEADLINE_YEARLY_RENT,
    HEADER_LEGAL_FEE_PER_LEASE,
    HEADER_ERV_CURRENCY
} from '../templates/headerTitles';

import {
    REPORT_SHEET_NAME,
    COLUMN_INDEXES,
    DATA_SHEET_NAME,
    DATA_TABLE_NAME,

    START_INDEX_KPI_TABLE,
    START_INDEX_GLA_TABLE,
    START_INDEX_REPORT_TABLE,

    START_INDEX_REPORT_RANGE_TABLE,
    START_INDEX_CURRENCY_CONVERSION,
    START_INDEX_INDEXATION_TABLE,
    START_INDEX_ADJUSTMENT_TABLE,
    START_INDEX_DEFAULT_CURRENCY,
    ADJUSTMENT_SHEET_NAME,
} from '../constants';

import { getEndDateOfMonth, checkMonthIsInLeaseProjection, getExchangeRates, excelDateToJSDate, getMonthDiff } from '../utils';
import { FILL_AZURE_LIGHT, FILL_YELLOW_LIGHT, FILL_GRAY_LIGHT, BORDER_AZURE, FONT_YELLOW, BORDER_LIGHT_GRAY, FONT_GRAY, FONT_BLACK } from '../templates/colors';

import { formatRangeBorder } from './formatRangeBorder';

import { COLUMN_COUNT } from '../constants';

/**
   * Render Report Range Table
   * @param {array} duration array of month
   */
export const renderReportRangeTable = duration => {
    const _self = this;
    Excel.run(async context => {
        var sheet = context.workbook.worksheets.getItem(REPORT_SHEET_NAME);
        let startColumn = START_INDEX_REPORT_RANGE_TABLE[0];
        let startRow = START_INDEX_REPORT_RANGE_TABLE[1];

        let startColumnIndex = COLUMN_INDEXES.indexOf(startColumn);

        let index = startColumn + startRow;
        var range = sheet.getRange(index);
        range.values = [["Report Date Range"]];

        range = sheet.getRange(index + ':' + COLUMN_INDEXES[startColumnIndex + COLUMN_COUNT] + startRow);
        range.merge();

        formatRangeBorder(range, BORDER_AZURE, 'Medium');

        let row = (startRow - 0 + 1);
        index = startColumn + row;
        range = sheet.getRange(index);
        range.values = [["Report Start Date"]];
        range = sheet.getRange(index + ':' + COLUMN_INDEXES[startColumnIndex + COLUMN_COUNT - 1] + row);
        range.merge();

        range = sheet.getRange(COLUMN_INDEXES[startColumnIndex + COLUMN_COUNT] + row);
        let startMonth = duration[0];

        let title = getEndDateOfMonth(startMonth[0], startMonth[1]);
        title = startMonth[1] + '/1/' + startMonth[0];
        range.values = [[title]]
        range.numberFormat = "dd/mm/yy";

        row = (startRow - 0 + 2);
        index = startColumn + row;
        range = sheet.getRange(index);
        range.values = [["Report End Date"]];
        range = sheet.getRange(index + ':' + COLUMN_INDEXES[startColumnIndex + COLUMN_COUNT - 1] + row);
        range.merge();

        range = sheet.getRange((startColumn + (row - 1)) + ':' + COLUMN_INDEXES[startColumnIndex + COLUMN_COUNT - 1] + row);
        range.format.fill.color = FILL_AZURE_LIGHT;

        formatRangeBorder(range, BORDER_AZURE, 'Medium');

        range = sheet.getRange(COLUMN_INDEXES[startColumnIndex + COLUMN_COUNT] + row);

        let endMonth = duration[duration.length - 1];
        title = getEndDateOfMonth(endMonth[0], endMonth[1] - 1);
        title = endMonth[1] + '/' + title + '/' + endMonth[0];
        range.values = [[title]]
        range.numberFormat = "dd/mm/yy";

        range = sheet.getRange(COLUMN_INDEXES[startColumnIndex + COLUMN_COUNT] + (row - 1) + ':' + COLUMN_INDEXES[startColumnIndex + COLUMN_COUNT] + row);
        range.format.fill.color = FILL_YELLOW_LIGHT;

        formatRangeBorder(range, BORDER_AZURE, 'Medium');

        return context.sync();
    });
}