import axios from 'axios';

/**
 * Get End Date of Month
 * @param {number} y year
 * @param {number} m month
 * @returns {date} last date of month
 */
export const getEndDateOfMonth = (y, m) => {
    let d = new Date(y, m + 1, 0);
    return d.getDate()
}

