/**
 * Get yyyy-mm-dd formatted string from date
 * @param {date} date date value to convert
 * @returns {string} yyyy-mm-dd formatted string
 */
export const getStringFromDate = date => {
    let y = date.getFullYear();
    let m = date.getMonth() + 1;
    let d = date.getDate();
    let str = y + '-' + (m < 10 ? ('0' + m) : m) + '-' + (d < 10 ? ('0' + d) : d);
    return str;
}

/**
 * Check whether current month is in between start date and end date
 * @param {date} startDate start date
 * @param {date} endDate end date
 * @param {array} month current month
 * @returns {boolean} true or false
 */
export const checkMonthIsInLeaseProjection = (startDate, endDate, month) => {
    let startOfMonth = new Date(month[0], month[1] - 1, 1);
    let endOfMonth = new Date(month[0], month[1], 0);
    if (endDate) {
        if (startOfMonth > startDate && endOfMonth < endDate) return true;
        else if (startOfMonth.getFullYear() == startDate.getFullYear() && startOfMonth.getMonth() == startDate.getMonth()) return true;
        else if (endOfMonth.getFullYear() == endDate.getFullYear() && endOfMonth.getMonth() == endDate.getMonth()) return true;
        else return false;
    } else {
        if (startOfMonth > startDate) return true;
        else if (startOfMonth.getFullYear() == startDate.getFullYear() && startOfMonth.getMonth() == startDate.getMonth()) return true;
        else return false;
    }
}

/**
 * Get Exchange Rate of currency
 * Default Currency is EUR
 * @param {string} defaultCurrency default currency
 * @param {array} currencies currency list to get rate
 * @returns {number} exchange rate
 */
export const getExchangeRates = async (defaultCurrency, currencies) => {
    return new Promise((resolve, reject) => {
        console.log('get exchange rate')
        // currencies.remove(defaultCurrency);
        let filteredCurrencies = [];
        currencies.forEach(elem => elem != defaultCurrency && filteredCurrencies.push(elem));

        let now = new Date();

        let strNow = getStringFromDate(now);
        let url = 'https://api.exchangeratesapi.io/history?start_at=2010-01-01';
        url += '&end_at=' + strNow;
        url += '&base=' + defaultCurrency;
        url += '&symbols=' + filteredCurrencies.join(',');
        console.log('call api', url)
        axios
            .get(url)
            .then(res => {
                let data = res.data.rates;
                for (var key in data) {
                    data[key][defaultCurrency] = 1;
                }
                const ordered = {};
                Object.keys(data).sort().forEach(function (key) {
                    ordered[key] = data[key];
                });

                let final = [];
                for (var key in ordered) {
                    final.push({
                        date: key,
                        rates: ordered[key],
                    })
                }
                resolve(final)
            })
            .catch(err => {
                console.error('failed fetching exchange data')
                resolve(null);
            })
    })
}

/**
 * Get Date from excel date
 * @param {number} serial int value of excel date
 * @returns {date}
 */
export const excelDateToJSDate = serial => {
    if (typeof serial != 'number') return null;
    var utc_days = Math.floor(serial - 25569);
    var utc_value = utc_days * 86400;
    var date_info = new Date(utc_value * 1000);

    return new Date(date_info.getFullYear(), date_info.getMonth(), date_info.getDate());
}

/**
 * Get count of months between start date and end date
 * @param {date} startDate start date
 * @param {date} endDate end date
 */
export const getMonthDiff = (startDate, endDate) => {
    var months;
    months = (endDate.getFullYear() - startDate.getFullYear()) * 12;
    months -= startDate.getMonth();
    months += endDate.getMonth() + 1;
    return months <= 0 ? 0 : months;
}

