import {
  getMonthDiff
} from '../utils';

import {
  HEADER_INDEXATION,
  HEADER_NEXT_INDEXATION,
  HEADER_CURRENCY,
  HEADER_LEASE_START,
  HEADER_LEASE_EXPIRY,
  HEADER_RENT_INCOME_MONTHLY,
  HEADER_ESTIMATED_VOID_PERIOD_MONTHS,
  HEADER_ERV_FOR_NEW_LEASE_MONTH,
  HEADER_NEW_LEASE_TERM_MONTHS,
  HEADER_ESTIMATED_INDEFINITE_PERIOD_END,
  HEADER_GROSS_LETTABLE_AREA_SQM,
  HEADER_TENANT_NAME,
  HEADER_TYPE_OF_AREA,
  HEADER_BREAK_OPTION_DATE,
  HEADER_AGENT_FEE_OF_HEADLINE_YEARLY_RENT,
  HEADER_LEGAL_FEE_PER_LEASE,
  HEADER_ERV_CURRENCY,
  HEADER_FILL_OUT_CONTRIB_NET,
  HEADER_NEW_LEASE_RENTAL_DISCOUNT_MONTHS,
  HEADER_NEW_LEASE_RENTAL_DISCOUNT
} from '../templates/headerTitles';

import {
  getRentalDiscountsAndSteppedRents,
} from './projection';

import {
  COLUMN_INDEXES,
} from '../constants';
/**
 * Get Adjustment Data from projection list
 * @param {array} data data with projection list
 * @param {array} duration month array
 * @param {number} startRow
 * @param {number} startColumnIndex
 * @returns {array} adjustment data
 */
export const getAdjustmentData = (data, duration, row, startColumnIndex) => {
  let adjustmentData = {};
  let metaData = {
    currentLease: {},
    newLease: {}
  };
  let formats = {};

  let lastProjectionMonth = new Date(
    duration[duration.length - 1][0],
    duration[duration.length - 1][1],
    0
  );

  let projectionList = data['projection-list'];

  let rentIncomeMonthly = data[HEADER_RENT_INCOME_MONTHLY.toLowerCase()];
  let GLA = data[HEADER_GROSS_LETTABLE_AREA_SQM.toLowerCase()];

  let leaseStart = data[HEADER_LEASE_START.toLowerCase()];
  let leaseEnd = data[HEADER_LEASE_EXPIRY.toLowerCase()];

  let breakOptionDate = data[HEADER_BREAK_OPTION_DATE.toLowerCase()];

  let ERVForNewLease_Month = data[HEADER_ERV_FOR_NEW_LEASE_MONTH.toLowerCase()];

  let fillOutContribution_net = data[HEADER_FILL_OUT_CONTRIB_NET.toLowerCase()];

  let agentFeePercent = data[HEADER_AGENT_FEE_OF_HEADLINE_YEARLY_RENT.toLowerCase()];
  let legalFee = data[HEADER_LEGAL_FEE_PER_LEASE.toLowerCase()];

  let estimatedIndefinitePeriodEnd = data[HEADER_ESTIMATED_INDEFINITE_PERIOD_END.toLowerCase()];
  if (leaseEnd == null) leaseEnd = estimatedIndefinitePeriodEnd;

  let currentLeaseTerm = getMonthDiff(leaseStart, leaseEnd);

  let newLeaseTermStart = new Date(leaseEnd.getTime());
  newLeaseTermStart.setMonth(newLeaseTermStart.getMonth() + 1);

  let newLeaseTerm = getMonthDiff(newLeaseTermStart, lastProjectionMonth);
  let newLeaseRentalDiscountMonths = 0;
  // newRentalDiscounts
  for (var rental of data.newRentalDiscounts) {
    if (rental[1] != null && rental[2] != null) {
      newLeaseRentalDiscountMonths += (getMonthDiff(rental[1], rental[2])) * rental[0] / ERVForNewLease_Month;
    }
  }
  let newLeaseRentalDiscount = data[HEADER_NEW_LEASE_RENTAL_DISCOUNT.toLowerCase()];

  let rentalDiscountMonths = 0;
  let { rentalDiscounts, steppedRents } = getRentalDiscountsAndSteppedRents(data);
  console.log(data, rentalDiscounts)
  for (var rental of rentalDiscounts) {
    if (rental[1] != null && rental[2] != null) {
      rentalDiscountMonths += (getMonthDiff(rental[1], rental[2])) * rental[0] / rentIncomeMonthly;
    }
  }
  let totalAverageRent = rentIncomeMonthly * (currentLeaseTerm - rentalDiscountMonths);

  let averageRentMonthly = totalAverageRent / currentLeaseTerm;

  let totalErvAverageRent = ERVForNewLease_Month * (newLeaseTerm - newLeaseRentalDiscountMonths);
  let averageErvAverageRentMonthly = totalErvAverageRent / newLeaseTerm;

  metaData.currentLease.leaseStart = leaseStart;
  metaData.currentLease.leaseEnd = leaseEnd;
  metaData.currentLease.rentalDiscounts = rentalDiscounts;
  metaData.currentLease.GLA = GLA;
  metaData.currentLease.rentIncomeMonthly = rentIncomeMonthly;
  metaData.currentLease.totalAverageRent = totalAverageRent;
  metaData.currentLease.totalAgentFee = 0;
  metaData.currentLease.totalLegalFee = 0;
  metaData.currentLease.totalFitOutContribution = 0;

  metaData.newLease.leaseStart = newLeaseTermStart;
  metaData.newLease.leaseEnd = lastProjectionMonth;
  metaData.newLease.rentalDiscounts = data.newRentalDiscounts;
  metaData.newLease.GLA = GLA;
  metaData.newLease.rentIncomeMonthly = ERVForNewLease_Month;
  metaData.newLease.totalAverageRent = totalErvAverageRent;
  metaData.newLease.totalAgentFee = ERVForNewLease_Month * 12 * agentFeePercent;
  metaData.newLease.totalLegalFee = legalFee;
  metaData.newLease.totalFitOutContribution = fillOutContribution_net;

  let arrAverageRent = [];
  let arrInvoicedRent = [];
  let arrRentDiscountAdjustment = [];
  let arrAdjustmentAgentFees = [];
  let arrAdjustmentLegalFees = [];
  let arrFitOutContribution = [];
  let arrExAverageRent = [];
  let arrExInvoicedRent = [];
  let arrExRentDiscountAdjustment = [];
  let arrExAdjustmentAgentFees = [];
  let arrExAdjustmentLegalFees = [];
  let arrExFitOutContribution = [];

  console.log(projectionList)
  let columnIndex = startColumnIndex - 0 + 6;

  for (var month of duration) {
    let key = month[0] + '-' + month[1];
    let item = projectionList[key];

    let averageRent = null;
    let invoicedRent = null;
    let rentDiscountAdjustment = null;
    let adjustmentAgentFees = null;
    let adjustmentLegalFees = null;
    let fitOutContribution = null;
    let exAverageRent = null;
    let exInvoicedRent = null;
    let exRentDiscountAdjustment = null;
    let exAdjustmentAgentFees = null;
    let exAdjustmentLegalFees = null;
    let exFitOutContribution = null;
    switch (item.type) {
      case 'break':
        averageRent = 0;
        invoicedRent = 0;
        rentDiscountAdjustment = `=${COLUMN_INDEXES[columnIndex] + row}-${COLUMN_INDEXES[columnIndex] + (row - 0 + 1)}`;
        adjustmentAgentFees = 0;
        adjustmentLegalFees = 0;
        fitOutContribution = 0;
        break;
      case 'income':
        averageRent = averageRentMonthly / item.exchangeRate;
        invoicedRent = rentIncomeMonthly / item.exchangeRate;
        rentDiscountAdjustment = `=${COLUMN_INDEXES[columnIndex] + row}-${COLUMN_INDEXES[columnIndex] + (row - 0 + 1)}`;
        adjustmentAgentFees = `=-${COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 3)}/${currentLeaseTerm}`;
        adjustmentLegalFees = `=-${COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 4)}/${currentLeaseTerm}`;
        fitOutContribution = `=-${COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 5)}/${currentLeaseTerm}`;
        break;
      case 'indexation':
        averageRent = averageRentMonthly / item.exchangeRate;
        invoicedRent = rentIncomeMonthly / item.exchangeRate;
        rentDiscountAdjustment = `=${COLUMN_INDEXES[columnIndex] + row}-${COLUMN_INDEXES[columnIndex] + (row - 0 + 1)}`;
        adjustmentAgentFees = `=-${COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 3)}/${currentLeaseTerm}`;
        adjustmentLegalFees = `=-${COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 4)}/${currentLeaseTerm}`;
        fitOutContribution = `=-${COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 5)}/${currentLeaseTerm}`;
        break;
      case 'srent':
        averageRent = averageRentMonthly / item.exchangeRate;
        invoicedRent = rentIncomeMonthly / item.exchangeRate;
        rentDiscountAdjustment = `=${COLUMN_INDEXES[columnIndex] + row}-${COLUMN_INDEXES[columnIndex] + (row - 0 + 1)}`;
        adjustmentAgentFees = `=-${COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 3)}/${currentLeaseTerm}`;
        adjustmentLegalFees = `=-${COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 4)}/${currentLeaseTerm}`;
        fitOutContribution = `=-${COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 5)}/${currentLeaseTerm}`;
        break;
      case 'rental':
        averageRent = averageRentMonthly / item.exchangeRate;
        invoicedRent = (rentIncomeMonthly - item['rental']) / item.exchangeRate;
        rentDiscountAdjustment = `=${COLUMN_INDEXES[columnIndex] + row}-${COLUMN_INDEXES[columnIndex] + (row - 0 + 1)}`;
        adjustmentAgentFees = `=-${COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 3)}/${currentLeaseTerm}`;
        adjustmentLegalFees = `=-${COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 4)}/${currentLeaseTerm}`;
        fitOutContribution = `=-${COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 5)}/${currentLeaseTerm}`;
        break;
      case 'erv-break':
        exAverageRent = 0;
        exInvoicedRent = 0;
        exRentDiscountAdjustment = 0;
        exAdjustmentAgentFees = 0;
        exAdjustmentLegalFees = 0;
        exFitOutContribution = 0;
        break;
      case 'erv':
        exAverageRent = averageErvAverageRentMonthly / item.exchangeRate;
        exInvoicedRent = ERVForNewLease_Month / item.exchangeRate;
        exRentDiscountAdjustment = `=${COLUMN_INDEXES[columnIndex] + (row - 0 + 6)}-${COLUMN_INDEXES[columnIndex] + (row - 0 + 7)}`;
        exAdjustmentAgentFees = `=-${COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 9)}/${newLeaseTerm}`;
        exAdjustmentLegalFees = `=-${COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 10)}/${newLeaseTerm}`;
        exFitOutContribution = `=-${COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 11)}/${newLeaseTerm}`;
        break;
      case 'erv-rental':
        exAverageRent = averageErvAverageRentMonthly / item.exchangeRate;
        exInvoicedRent = (ERVForNewLease_Month - newLeaseRentalDiscount) / item.exchangeRate;
        exRentDiscountAdjustment = `=${COLUMN_INDEXES[columnIndex] + (row - 0 + 6)}-${COLUMN_INDEXES[columnIndex] + (row - 0 + 7)}`;
        exAdjustmentAgentFees = `=-${COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 9)}/${newLeaseTerm}`;
        exAdjustmentLegalFees = `=-${COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 10)}/${newLeaseTerm}`;
        exFitOutContribution = `=-${COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 11)}/${newLeaseTerm}`;
        break;
      case 'erv-indexation':
        exAverageRent = averageErvAverageRentMonthly / item.exchangeRate;
        exInvoicedRent = ERVForNewLease_Month / item.exchangeRate;
        exRentDiscountAdjustment = `=${COLUMN_INDEXES[columnIndex] + (row - 0 + 6)}-${COLUMN_INDEXES[columnIndex] + (row - 0 + 7)}`;
        exAdjustmentAgentFees = `=-${COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 9)}/${newLeaseTerm}`;
        exAdjustmentLegalFees = `=-${COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 10)}/${newLeaseTerm}`;
        exFitOutContribution = `=-${COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 11)}/${newLeaseTerm}`;
        break;
      default:
        break;
    }

    arrAverageRent.push(averageRent);
    arrInvoicedRent.push(invoicedRent);
    arrRentDiscountAdjustment.push(rentDiscountAdjustment);
    arrAdjustmentAgentFees.push(adjustmentAgentFees);
    arrAdjustmentLegalFees.push(adjustmentLegalFees);
    arrFitOutContribution.push(fitOutContribution);
    arrExAverageRent.push(exAverageRent);
    arrExInvoicedRent.push(exInvoicedRent);
    arrExRentDiscountAdjustment.push(exRentDiscountAdjustment);
    arrExAdjustmentAgentFees.push(exAdjustmentAgentFees);
    arrExAdjustmentLegalFees.push(exAdjustmentLegalFees);
    arrExFitOutContribution.push(exFitOutContribution);

    columnIndex++;
  }
  adjustmentData = {
    arrAverageRent,
    arrInvoicedRent,
    arrRentDiscountAdjustment,
    arrAdjustmentAgentFees,
    arrAdjustmentLegalFees,
    arrFitOutContribution,
    arrExAverageRent,
    arrExInvoicedRent,
    arrExRentDiscountAdjustment,
    arrExAdjustmentAgentFees,
    arrExAdjustmentLegalFees,
    arrExFitOutContribution,
  }
  return {
    adjustmentData,
    metaData,
    formats
  };
}