import { formatRangeBorder } from './formatRangeBorder';

import { COLUMN_COUNT } from '../constants';

/**
 * Render Default Currency Table
 */
export const renderDefaultCurrencyTable = () => {
    const _self = this;
    Excel.run(async context => {
        var sheet = context.workbook.worksheets.getItem(REPORT_SHEET_NAME);
        let startColumn = START_INDEX_DEFAULT_CURRENCY[0];
        let startRow = START_INDEX_DEFAULT_CURRENCY[1];

        let startColumnIndex = COLUMN_INDEXES.indexOf(startColumn);

        let index = startColumn + startRow;
        var range = sheet.getRange(index);
        range.values = [["Default Currency"]];

        range = sheet.getRange(index + ':' + COLUMN_INDEXES[startColumnIndex + COLUMN_COUNT - 1] + startRow);
        range.merge();

        formatRangeBorder(range, BORDER_AZURE, 'Medium');

        range = sheet.getRange(COLUMN_INDEXES[startColumnIndex + COLUMN_COUNT] + startRow);
        range.values = [['EUR']];
        range.format.fill.color = FILL_YELLOW_LIGHT;
        formatRangeBorder(range, BORDER_AZURE, 'Medium');
        //EUR,USD,AUD,BGN,BRL,CAD,CHF,CNY,CZK,DKK,GBP,HKD,HRK,HUF,IDR,ILS,INR,ISK,JPY,KRW,MXN,MYR,NOK,NZD,PHP,PLN,RON,RUB,SEK,SGD,THB,TRY,ZAR
        range.dataValidation.rule = {
            list: {
                inCellDropDown: true,
                source: "EUR,USD,AUD,BGN,BRL,CAD,CHF,CNY,CZK,DKK,GBP,HKD,HRK,HUF,IDR,ILS,INR,ISK,JPY,KRW,MXN,MYR,NOK,NZD,PHP,PLN,RON,RUB,SEK,SGD,THB,TRY,ZAR"
            }
        };
        return context.sync();
    })
}