import { formatRangeBorder } from './formatRangeBorder';

import { COLUMN_COUNT } from '../constants';
/**
   * Render Indexation Table
   */
export const renderLegendTable = () => {
    const _self = this;
    console.log('render indexation table')
    Excel.run(async context => {
        var sheet = context.workbook.worksheets.getItem(REPORT_SHEET_NAME);
        let startColumn = START_INDEX_REPORT_TABLE[0];
        let startRow = START_INDEX_REPORT_TABLE[1] - 13;

        let index = startColumn + startRow;
        let range = sheet.getRange(index)
        range.values = [['Key']];

        index = startColumn + (startRow - 0 + 1);
        range = sheet.getRange(index)
        range.values = [['Existing Data']];

        index = startColumn + (startRow - 0 + 2);
        range = sheet.getRange(index)
        range.values = [['Discount']];
        range.format.fill.color = 'lightgreen';
        range.format.font.color = 'green';

        index = startColumn + (startRow - 0 + 3);
        range = sheet.getRange(index)
        range.values = [['Stepped']];
        range.format.font.color = "red";
        range.format.fill.color = "lightpink";

        index = startColumn + (startRow - 0 + 4);
        range = sheet.getRange(index)
        range.values = [['Indexation']];
        range.format.font.color = FONT_YELLOW;
        range.format.fill.color = FILL_GRAY_LIGHT;

        index = startColumn + (startRow - 0 + 5);
        range = sheet.getRange(index)
        range.values = [['Forcasted']];
        range.format.fill.color = FILL_GRAY_LIGHT;

        index = startColumn + (startRow - 0 + 6);
        range = sheet.getRange(index)
        range.values = [['Break Notice']];
        range.format.font.color = 'red';

        index = startColumn + startRow + ':' + startColumn + (startRow - 0 + 6);
        formatRangeBorder(sheet.getRange(index), BORDER_AZURE, 'medium');

        return context.sync();
    });
}