import {
  HEADER_INDEXATION,
  HEADER_NEXT_INDEXATION,
  HEADER_CURRENCY,
  HEADER_LEASE_START,
  HEADER_LEASE_EXPIRY,
  HEADER_RENT_INCOME_MONTHLY,
  HEADER_ESTIMATED_VOID_PERIOD_MONTHS,
  HEADER_ERV_FOR_NEW_LEASE_MONTH,
  HEADER_NEW_LEASE_TERM_MONTHS,
  HEADER_ESTIMATED_INDEFINITE_PERIOD_END,
  HEADER_GROSS_LETTABLE_AREA_SQM,
  HEADER_TENANT_NAME,
  HEADER_TYPE_OF_AREA,
  HEADER_BREAK_OPTION_DATE,
  HEADER_AGENT_FEE_OF_HEADLINE_YEARLY_RENT,
  HEADER_LEGAL_FEE_PER_LEASE,
  HEADER_ERV_CURRENCY,
  HEADER_FILL_OUT_CONTRIB_CURRENCY,
  HEADER_NEW_LEASE_RENTAL_DISCOUNT,
  HEADER_NEW_LEASE_RENTAL_DISCOUNT_MONTHS
} from '../templates/headerTitles';

import {
  TEMPLATE_NEW_RENTAL_DISCOUNT,
  TEMPLATE_NEW_STEPPED_RENT
} from '../templates/newColumns';

import {
  checkMonthIsInLeaseProjection,
  searchCurrencyExchangeRateByDate,
  getEndDateOfMonth,
} from '../utils'

/**
 * Get Currencies From Table Data
 * @param {string} defaultCurrency default currency
 * @param {array} tableData array of table data
 * @returns {array} array of currency string
 */
export const getCurrenciesFromTableData = (defaultCurrency, tableData) => {
  let currencies = [];

  tableData.forEach(elem => {
    // currency
    if (
      !currencies.includes(elem[HEADER_CURRENCY.toLowerCase()].toUpperCase())
      && typeof elem[HEADER_CURRENCY.toLowerCase()] == 'string'
      && elem[HEADER_CURRENCY.toLowerCase()] != ""
    ) currencies.push(elem[HEADER_CURRENCY.toLowerCase()].toUpperCase());

    // erv currency
    if (
      !currencies.includes(elem[HEADER_ERV_CURRENCY.toLowerCase()].toUpperCase())
      && typeof elem[HEADER_ERV_CURRENCY.toLowerCase()] == 'string'
      && elem[HEADER_ERV_CURRENCY.toLowerCase()] != ""
    ) currencies.push(elem[HEADER_ERV_CURRENCY.toLowerCase()].toUpperCase());

    // fit out contribution currency
    if (
      !currencies.includes(elem[HEADER_FILL_OUT_CONTRIB_CURRENCY.toLowerCase()].toUpperCase())
      && typeof elem[HEADER_FILL_OUT_CONTRIB_CURRENCY.toLowerCase()] == 'string'
      && elem[HEADER_FILL_OUT_CONTRIB_CURRENCY.toLowerCase()] != ""
    ) currencies.push(elem[HEADER_FILL_OUT_CONTRIB_CURRENCY.toLowerCase()].toUpperCase());
  })
  if (!currencies.includes(defaultCurrency)) currencies.push(defaultCurrency);
  return currencies;
}

/**
   * Get Lease Projection Duration
   * @param {array} data arranged data
   * @returns {array} array of months
   */
export const getLeaseProjectionDuration = data => {
  let start = null;
  let end = null;
  console.log('get duration')
  for (var i in data) {
    let startDate = data[i][HEADER_LEASE_START.toLowerCase()];
    let d = startDate;
    if (d) {
      if (start == null) {
        start = d;
      } else {
        if (d.getTime() < start.getTime()) {
          start = d;
        }
      }
    }
    let expiryDate = data[i][HEADER_LEASE_EXPIRY.toLowerCase()];
    d = expiryDate;
    if (d) {
      if (end == null) {
        end = d;
      } else {
        if (d.getTime() > end.getTime()) end = d;
      }
    }
  }

  let months = [];
  let startYear = start.getFullYear();
  let startMonth = start.getMonth() + 1;
  let endYear = end.getFullYear();
  let endMonth = end.getMonth() + 1;

  for (var i = startMonth; i <= 12; i++) {
    months.push([startYear, i])
  }
  for (var i = startYear + 1; i < endYear; i++) {
    for (var j = 1; j <= 12; j++) {
      months.push([i, j])
    }
  }
  for (var i = 1; i <= endMonth; i++) {
    months.push([endYear, i]);
  }
  return months;
}

/**
   * Get Rental Discounts and Stepped Rents
   * @param {object} data object data
   * @returns {object} {Rental Discounts, Stepped Rents}
   * Rental Discounts : Array
   * Stepped Rents : Object
   */
export const getRentalDiscountsAndSteppedRents = data => {
  let rentalDiscounts = [];
  let steppedRents = [];
  for (let key in data) {
    if (key.includes(TEMPLATE_NEW_RENTAL_DISCOUNT.prefix.toLowerCase()) && key.includes(' start')) {
      let prefix = key.replace(' start', '');
      rentalDiscounts.push([
        data[prefix],
        data[prefix + ' start'],
        data[prefix + ' end']
      ])
    }
    if (key.includes(TEMPLATE_NEW_STEPPED_RENT.prefix.toLowerCase()) && key.includes(' start')) {
      let prefix = key.replace(' start', '');
      steppedRents.push([
        data[prefix],
        data[prefix + ' start'],
        data[prefix + ' end']
      ])
    }
  }

  let newRentalDiscounts = [];
  let newSteppedRents = [];
  for (var item of rentalDiscounts) {
    if (item[1] != null && item[2] != null) newRentalDiscounts.push(item);
  }
  for (var item of steppedRents) {
    if (item[1] != null && item[2] != null) newSteppedRents.push(item);
  }
  return {
    rentalDiscounts: newRentalDiscounts,
    steppedRents: newSteppedRents
  }
}

/**
 * Get next indexation date
 * @param {object} data
 * If "indexation" field set "Yes" return next indexation date
 * Else return null
 * @returns {date or null} next indexation date or null
 */
export const getIndexationDate = data => {
  if (typeof data[HEADER_INDEXATION.toLowerCase()] == 'string' && data[HEADER_INDEXATION.toLowerCase()].toLowerCase() == 'yes') {
    return data[HEADER_NEXT_INDEXATION.toLowerCase()]
  }
  return null;
}

/**
   * Get indexation value from Indexation Table
   * @param {object} indexationData
   * @param {number} year year to get
   * @returns {number} indexation value
   */
export const getIndexationDataFromTable = (indexationData, year) => {
  if (indexationData == undefined || indexationData == null) return 0.02;
  else {
    let rate = 0.02;
    if (year < new Date().getFullYear()) rate = indexationData['<-Earlier'];
    else {
      for (var y in indexationData) {
        rate = indexationData[y];
        if (y == year) break;
      }
      console.log(year, rate);
      return rate;
    }
  }
}

/**
   * Get Lease Projection Data
   * @param {array} data json array data
   * @param {array} duration array of month
   * @param {array} exchangeData array of exchange data
   * @param {object} indexationData array of indexation data
   * @returns {array} json data with projection list
   */
export const getLeaseProjectionData = (data, duration, exchangeData, indexationData) => {
  const _self = this;
  let arrangedData = [];
  console.log('indexation data for lease projection', indexationData)
  // render data
  let lastDateOfProjection = new Date(duration[duration.length - 1][0], duration[duration.length - 1][1], 0);
  for (var i = 0; i < data.length; i++) {
    let arrangedDataItem = {
      ...data[i]
    }

    let leaseStartDate = data[i][HEADER_LEASE_START.toLowerCase()];
    let leaseEndDate = data[i][HEADER_LEASE_EXPIRY.toLowerCase()];

    let rentIncomeMonthly = data[i][HEADER_RENT_INCOME_MONTHLY.toLowerCase()];
    let { rentalDiscounts, steppedRents } = getRentalDiscountsAndSteppedRents(data[i]);

    // // get exchange rate
    let currency = data[i][HEADER_CURRENCY.toLowerCase()].toUpperCase();
    let exchangeRate = searchCurrencyExchangeRateByDate(exchangeData, currency);

    // end get exchange rate
    let indexationDate = getIndexationDate(data[i]);
    let indexationRate = 1;
    let indexationRateForNewLease = 1;

    let estimatedVoidPeriod = data[i][HEADER_ESTIMATED_VOID_PERIOD_MONTHS.toLowerCase()];
    let ERVforNewLease = data[i][HEADER_ERV_FOR_NEW_LEASE_MONTH.toLowerCase()];
    let newLeaseTerm = data[i][HEADER_NEW_LEASE_TERM_MONTHS.toLowerCase()];

    let newLeaseRentalDiscount = data[i][HEADER_NEW_LEASE_RENTAL_DISCOUNT.toLowerCase()];
    let newLeaseRentalDiscountMonths = data[i][HEADER_NEW_LEASE_RENTAL_DISCOUNT_MONTHS.toLowerCase()];

    let estimatedIndefinitePeriodEnd = data[i][HEADER_ESTIMATED_INDEFINITE_PERIOD_END.toLowerCase()];

    if (!leaseEndDate) leaseEndDate = estimatedIndefinitePeriodEnd;

    let nextVoidPeriodStartDate = new Date(leaseEndDate.getFullYear(), leaseEndDate.getMonth(), leaseEndDate.getDate() + 1);

    let nextLeaseStartDate = new Date(nextVoidPeriodStartDate.getTime());
    nextLeaseStartDate.setMonth(nextLeaseStartDate.getMonth() + estimatedVoidPeriod);

    let nextLeaseDiscountStart = null;
    let nextLeaseDiscountEnd = null;
    if (newLeaseRentalDiscountMonths > 0) {
      nextLeaseDiscountStart = new Date(nextLeaseStartDate.getTime());
      nextLeaseDiscountEnd = new Date(nextLeaseStartDate.getTime());

      nextLeaseDiscountEnd.setMonth(nextLeaseDiscountEnd.getMonth() + newLeaseRentalDiscountMonths);
      nextLeaseDiscountEnd.setDate(nextLeaseDiscountEnd.getDate() - 1);
    }

    console.log(nextLeaseDiscountStart, nextLeaseDiscountEnd)
    let nextLeaseEndDate = new Date(nextLeaseStartDate.getTime());
    nextLeaseEndDate.setMonth(nextLeaseEndDate.getMonth() + newLeaseTerm);
    nextLeaseEndDate.setDate(nextLeaseEndDate.getDate() - 1);

    arrangedDataItem['projection-list'] = {};

    let newRentalDiscounts = [];
    if (nextLeaseDiscountStart && nextLeaseDiscountStart < lastDateOfProjection) {
      newRentalDiscounts.push([
        newLeaseRentalDiscount,
        nextLeaseDiscountStart,
        nextLeaseDiscountEnd < lastDateOfProjection ? nextLeaseDiscountEnd : lastDateOfProjection
      ])
    }
    for (var month of duration) {
      /**
       * month[0] : year
       * month[1] : month
       */
      let startDateOfMonth = new Date(month[0], month[1] - 1, 1);
      let endDateOfMonth = new Date(month[0], month[1], 0);

      // current lease
      if (checkMonthIsInLeaseProjection(leaseStartDate, leaseEndDate, month)) {
        let value = 0;
        // check first month of current lease
        if (leaseStartDate.getFullYear() == month[0] && leaseStartDate.getMonth() == month[1] - 1) {
          value = rentIncomeMonthly * indexationRate / exchangeRate * (endDateOfMonth.getDate() - leaseStartDate.getDate() + 1) / endDateOfMonth.getDate();
        }
        // check last month of current lease
        else if (leaseEndDate.getFullYear() == month[0] && leaseEndDate.getMonth() == month[1] - 1) {
          value = rentIncomeMonthly * indexationRate / exchangeRate * leaseEndDate.getDate() / endDateOfMonth.getDate();
        }
        // else
        else {
          value = rentIncomeMonthly * indexationRate / exchangeRate;
        }
        arrangedDataItem['projection-list'][month[0] + '-' + month[1]] = {
          type: 'income',
          initial: rentIncomeMonthly,
          value: value,
          indexationRate,
          exchangeRate,
        }

        // check rental discount
        for (var rental of rentalDiscounts) {
          if (rental[1] && rental[1] != "" && rental[2] && rental[2] != "") {
            let rentalStart = rental[1];
            let rentalEnd = rental[2];
            if (checkMonthIsInLeaseProjection(rentalStart, rentalEnd, month)) {
              value = (rentIncomeMonthly - rental[0]) * indexationRate / exchangeRate;

              // start month of rental discount
              if (
                rentalStart.getFullYear() == month[0]
                && (rentalStart.getMonth() + 1) == month[1]
              ) {
                value = (
                  rentIncomeMonthly * indexationRate / exchangeRate
                  - rental[0] * indexationRate / exchangeRate * (endDateOfMonth.getDate() - rentalStart.getDate() + 1) / endDateOfMonth.getDate()
                )
              }
              // end month of rental discount
              else if (
                rentalEnd.getFullYear() == month[0]
                && (rentalEnd.getMonth() + 1) == month[1]
              ) {
                value = (
                  rentIncomeMonthly * indexationRate / exchangeRate
                  - rental[0] * indexationRate / exchangeRate * (rentalEnd.getDate()) / endDateOfMonth.getDate()
                )
              }

              arrangedDataItem['projection-list'][month[0] + '-' + month[1]] = {
                type: 'rental',
                initial: rentIncomeMonthly,
                value: value,
                rental: rental[0],
                indexationRate,
                exchangeRate,
              }
            }
          }
        }
        // end check rental discount

        // check stepped rent
        for (var sRent of steppedRents) {
          if (sRent[1] && sRent[1] != "" && sRent[2] && sRent[2] != "") {
            let sRentStart = sRent[1];
            let sRentEnd = sRent[2];
            if (checkMonthIsInLeaseProjection(sRentStart, sRentEnd, month)) {
              value = sRent[0] * indexationRate / exchangeRate;

              // start month of stepped rent
              if (
                sRentStart.getFullYear() == month[0]
                && (sRentStart.getMonth() + 1) == month[1]
              ) {
                value = (
                  rentIncomeMonthly * indexationRate / exchangeRate * (sRentStart.getDate() - 1)
                  + sRent[0] * (endDateOfMonth.getDate() - sRentStart.getDate() + 1)
                ) / endDateOfMonth.getDate();
              }
              // end month of stepped rent
              else if (
                sRentEnd.getFullYear() == month[0]
                && (sRentEnd.getMonth() + 1) == month[1]
              ) {
                value = (
                  rentIncomeMonthly * indexationRate / exchangeRate * (endDateOfMonth.getDate() - sRentEnd.getDate())
                  + sRent[0] * sRentEnd.getDate()
                ) / endDateOfMonth.getDate();
              }

              arrangedDataItem['projection-list'][month[0] + '-' + month[1]] = {
                type: 'srent',
                initial: rentIncomeMonthly,
                value: value,
                sRent: sRent[0],
                indexationRate,
                exchangeRate,
              }
            }
          }
        }
        // end check stepped rent

        // check indexation
        if (indexationDate) {
          if (
            indexationDate
            && indexationDate.getFullYear() <= month[0]
            && indexationDate.getMonth() == (month[1] - 1)
          ) {
            let indexation = getIndexationDataFromTable(indexationData, month[0]);
            indexationRate *= (1 + indexation);
            value = value * (indexationDate.getDate() - 1) / endDateOfMonth.getDate()
              + value * (endDateOfMonth.getDate() - indexationDate.getDate() + 1) / endDateOfMonth.getDate() * (1 + indexation);

            arrangedDataItem['projection-list'][month[0] + '-' + month[1]] = {
              type: 'indexation',
              initial: rentIncomeMonthly,
              value: value,
              indexationRate,
              exchangeRate,
            }
          }
        }
        // end check indexation
      } else {
        // future lease
        if (leaseEndDate && leaseEndDate <= startDateOfMonth) {
          // check void period
          if (
            nextVoidPeriodStartDate <= startDateOfMonth
            && nextLeaseStartDate > endDateOfMonth
          ) {
            arrangedDataItem['projection-list'][month[0] + '-' + month[1]] = {
              type: 'erv-break',
              value: null,
              exchangeRate,
            }
          }
          // end check void period
          else if (
            endDateOfMonth >= nextLeaseStartDate
            && startDateOfMonth <= nextLeaseEndDate
          ) {
            let bIsLastMonth = false;
            let type = 'erv'
            let value = ERVforNewLease / exchangeRate * indexationRateForNewLease;
            // check start month
            if (
              nextLeaseStartDate.getFullYear() == endDateOfMonth.getFullYear()
              && nextLeaseStartDate.getMonth() == endDateOfMonth.getMonth()
            ) {
              value *= (endDateOfMonth.getDate() - nextLeaseStartDate.getDate() + 1) / endDateOfMonth.getDate();
            }
            // end check start month
            // check end month
            else if (
              nextLeaseEndDate.getFullYear() == endDateOfMonth.getFullYear()
              && nextLeaseEndDate.getMonth() == endDateOfMonth.getMonth()
            ) {
              bIsLastMonth = true;
              value = value * nextLeaseEndDate.getDate() / endDateOfMonth.getDate();
            }
            // end check end month

            // check rental discount
            if (
              nextLeaseDiscountStart && nextLeaseDiscountEnd
              && checkMonthIsInLeaseProjection(nextLeaseDiscountStart, nextLeaseDiscountEnd, month)) {
              value = (ERVforNewLease - newLeaseRentalDiscount) * indexationRateForNewLease / exchangeRate;

              type = 'erv-rental'
              // start month of rental discount
              if (
                nextLeaseDiscountStart.getFullYear() == month[0]
                && (nextLeaseDiscountStart.getMonth() + 1) == month[1]
              ) {
                value = (ERVforNewLease - newLeaseRentalDiscount) * indexationRateForNewLease / exchangeRate * (endDateOfMonth.getDate() - nextLeaseDiscountStart.getDate() + 1) / endDateOfMonth.getDate()
              }
              // end month of rental discount
              else if (
                nextLeaseDiscountEnd.getFullYear() == month[0]
                && (nextLeaseDiscountEnd.getMonth() + 1) == month[1]
              ) {
                value = (
                  ERVforNewLease * indexationRateForNewLease / exchangeRate
                  - newLeaseRentalDiscount * indexationRateForNewLease / exchangeRate * (nextLeaseDiscountEnd.getDate()) / endDateOfMonth.getDate()
                )
              }
            }
            // end check rental discount

            // check indexation month
            if (
              startDateOfMonth.getMonth() == nextLeaseStartDate.getMonth()
              && startDateOfMonth.getFullYear() > nextLeaseStartDate.getFullYear()
            ) {
              type = 'erv-indexation';
              let currentIndexationDate = new Date(nextLeaseStartDate.getTime());
              currentIndexationDate.setFullYear(currentIndexationDate.getFullYear() + startDateOfMonth.getFullYear() - nextLeaseStartDate.getFullYear())

              // check if last month
              if (
                currentIndexationDate.getFullYear() == nextLeaseEndDate.getFullYear()
                && currentIndexationDate.getMonth() == nextLeaseEndDate.getMonth()
              ) {
                // value *= (currentIndexationDate.getDate() - 1) / endDateOfMonth.getDate();
              }
              else {
                let indexation = getIndexationDataFromTable(indexationData, month[0]);
                indexationRateForNewLease *= (1 + indexation);
                value = (
                  value * (currentIndexationDate.getDate() - 1)
                  + value * (endDateOfMonth.getDate() - currentIndexationDate.getDate() + 1) * (1 + indexation)
                ) / endDateOfMonth.getDate();
              }
            }
            // end check indexation

            arrangedDataItem['projection-list'][month[0] + '-' + month[1]] = {
              type,
              initial: ERVforNewLease,
              value: value,
              indexationRateForNewLease,
              exchangeRate,
            }

            if (bIsLastMonth) {
              indexationRateForNewLease = 1;
              nextVoidPeriodStartDate = new Date(nextLeaseEndDate.getTime());
              nextVoidPeriodStartDate.setDate(nextVoidPeriodStartDate.getDate() + 1);

              nextLeaseStartDate = new Date(nextVoidPeriodStartDate.getTime());
              nextLeaseStartDate.setMonth(nextLeaseStartDate.getMonth() + estimatedVoidPeriod);

              nextLeaseEndDate = new Date(nextLeaseStartDate.getTime());
              nextLeaseEndDate.setMonth(nextLeaseEndDate.getMonth() + newLeaseTerm);
              nextLeaseEndDate.setDate(nextLeaseEndDate.getDate() - 1);

              if (newLeaseRentalDiscountMonths > 0) {
                nextLeaseDiscountStart = new Date(nextLeaseStartDate.getTime());
                nextLeaseDiscountEnd = new Date(nextLeaseStartDate.getTime());

                nextLeaseDiscountEnd.setMonth(nextLeaseDiscountEnd.getMonth() + newLeaseRentalDiscountMonths);
                nextLeaseDiscountEnd.setDate(nextLeaseDiscountEnd.getDate() - 1);

                if (nextLeaseDiscountStart && nextLeaseDiscountStart < lastDateOfProjection) {
                  newRentalDiscounts.push([
                    newLeaseRentalDiscount,
                    nextLeaseDiscountStart,
                    nextLeaseDiscountEnd < lastDateOfProjection ? nextLeaseDiscountEnd : lastDateOfProjection
                  ])
                }
              }
            }
          }
        }
        // before lease
        else {
          arrangedDataItem['projection-list'][month[0] + '-' + month[1]] = {
            type: 'break',
            value: null,
            exchangeRate,
          }
        }
      }
    }
    arrangedDataItem.newRentalDiscounts = newRentalDiscounts;
    arrangedData.push(arrangedDataItem);
  }
  return arrangedData
}