import {
    HEADER_INDEXATION,
    HEADER_NEXT_INDEXATION,
    HEADER_CURRENCY,
    HEADER_LEASE_START,
    HEADER_LEASE_EXPIRY,
    HEADER_RENT_INCOME_MONTHLY,
    HEADER_ESTIMATED_VOID_PERIOD_MONTHS,
    HEADER_ERV_FOR_NEW_LEASE_MONTH,
    HEADER_NEW_LEASE_TERM_MONTHS,
    HEADER_ESTIMATED_INDEFINITE_PERIOD_END,
    HEADER_GROSS_LETTABLE_AREA_SQM,
    HEADER_TENANT_NAME,
    HEADER_TYPE_OF_AREA,
    HEADER_BREAK_OPTION_DATE,
    HEADER_AGENT_FEE_OF_HEADLINE_YEARLY_RENT,
    HEADER_LEGAL_FEE_PER_LEASE,
    HEADER_ERV_CURRENCY
} from '../templates/headerTitles';

import {
    REPORT_SHEET_NAME,
    COLUMN_INDEXES,
    DATA_SHEET_NAME,
    DATA_TABLE_NAME,

    START_INDEX_KPI_TABLE,
    START_INDEX_GLA_TABLE,
    START_INDEX_REPORT_TABLE,

    START_INDEX_REPORT_RANGE_TABLE,
    START_INDEX_CURRENCY_CONVERSION,
    START_INDEX_INDEXATION_TABLE,
    START_INDEX_ADJUSTMENT_TABLE,
    START_INDEX_DEFAULT_CURRENCY,
    ADJUSTMENT_SHEET_NAME,
} from '../constants';

import { getEndDateOfMonth, checkMonthIsInLeaseProjection, getExchangeRates, excelDateToJSDate, getMonthDiff } from '../utils';
import { FILL_AZURE_LIGHT, FILL_YELLOW_LIGHT, FILL_GRAY_LIGHT, BORDER_AZURE, FONT_YELLOW, BORDER_LIGHT_GRAY, FONT_GRAY, FONT_BLACK } from '../templates/colors';

/**
  * Render Lease Projection Table
  * @param {array} data json array data
  * @param {array} duration array of months
  */
export const renderLeaseProjectionTable = (data, duration) => {
    console.log('render lease projection table')

    Excel.run(async context => {
        var sheet = context.workbook.worksheets.getItem(REPORT_SHEET_NAME);
        let startColumn = START_INDEX_REPORT_TABLE[0];
        let startRow = START_INDEX_REPORT_TABLE[1];

        let startColumnIndex = COLUMN_INDEXES.indexOf(startColumn);

        let index = startColumn + startRow + ':' + startColumn + (startRow - 0 + 1);
        var range = sheet.getRange(index);
        range.values = [["Start"], ["End"]];
        range.format.fill.color = FILL_AZURE_LIGHT;

        index = COLUMN_INDEXES[startColumnIndex + 1] + startRow + ':' + COLUMN_INDEXES[startColumnIndex + 1] + (startRow - 0 + 1);
        sheet.getRange(index).format.fill.color = FILL_AZURE_LIGHT;
        // render tenant area
        let value = []
        for (var i in data) {
            let tmp = [];
            tmp.push(data[i][HEADER_TENANT_NAME.toLowerCase()]);
            tmp.push(data[i][HEADER_TYPE_OF_AREA.toLowerCase()]);
            value.push(tmp);
        }
        index = startColumn + (startRow - 0 + 2) + ':' + COLUMN_INDEXES[startColumnIndex + 1] + (startRow - 0 + 1 + data.length)
        range = sheet.getRange(index);
        range.values = value;
        range.format.fill.color = FILL_AZURE_LIGHT;
        // render tenant area

        // render start/end month duration
        let startTitleArray = [];
        let endTitleArray = [];
        for (var i = 0; i < duration.length; i++) {

            let endDate = getEndDateOfMonth(duration[i][0], duration[i][1] - 1);
            startTitleArray.push('01.' + (duration[i][1] < 10 ? ('0' + duration[i][1]) : duration[i][1]) + '.' + (duration[i][0] - 2000));
            endTitleArray.push((endDate < 10 ? ('0' + endDate) : endDate) + '.' + (duration[i][1] < 10 ? ('0' + duration[i][1]) : duration[i][1]) + '.' + (duration[i][0] - 2000));
        }
        value = [startTitleArray, endTitleArray];
        index = COLUMN_INDEXES[startColumnIndex + 2] + startRow + ':' + COLUMN_INDEXES[startColumnIndex + 1 + duration.length] + (startRow - 0 + 1);
        range = sheet.getRange(index);
        range.values = value;
        range.format.fill.color = "lightgray";
        // end render start/end month duration

        // render data
        for (var i = 0; i < data.length; i++) {
            // render data by months
            let row = startRow + 2 + i;
            let projectionList = data[i]['projection-list'];

            let value = [];
            let columnOffset = 0;

            for (var key in projectionList) {
                let item = projectionList[key];

                let column = COLUMN_INDEXES[startColumnIndex + 2 + columnOffset];
                let index = column + row;

                switch (item.type) {
                    case 'break':
                        value.push("VACANT");
                        range = sheet.getRange(index);
                        range.format.font.color = "gray";
                        range.format.font.italic = true;
                        break;
                    case 'rental':
                        value.push(item.value);
                        range = sheet.getRange(index);
                        range.format.font.color = "green";
                        range.format.fill.color = "lightgreen";
                        range.format.font.italic = false;
                        break;
                    case 'srent':
                        value.push(item.value);
                        range = sheet.getRange(index);
                        range.format.font.color = "red";
                        range.format.fill.color = "lightpink";
                        range.format.font.italic = false;
                        break;
                    case 'indexation':
                        value.push(item.value);
                        range = sheet.getRange(index);
                        range.format.font.color = FONT_YELLOW;
                        range.format.fill.color = FILL_GRAY_LIGHT;
                        range.format.font.italic = false;
                        break;
                    case 'erv':
                        value.push(item.value);
                        range = sheet.getRange(index);
                        range.format.font.color = "black";
                        range.format.font.italic = false;
                        range.format.fill.color = FILL_AZURE_LIGHT;
                        break;
                    case 'erv-rental':
                        value.push(item.value);
                        range = sheet.getRange(index);
                        range.format.font.color = "green";
                        range.format.fill.color = "lightgreen";
                        range.format.font.italic = false;
                        break;
                    case 'erv-indexation':
                        value.push(item.value);
                        range = sheet.getRange(index);
                        range.format.font.color = FONT_YELLOW;
                        range.format.fill.color = FILL_GRAY_LIGHT;
                        range.format.font.italic = false;
                        break;
                    case 'erv-break':
                        value.push("VACANT");
                        range = sheet.getRange(index);
                        range.format.font.color = "gray";
                        range.format.font.italic = true;
                        range.format.fill.color = FILL_AZURE_LIGHT;
                        break;
                    default:
                        value.push(item.value);
                        break;
                }
                columnOffset++;
            }
            index = COLUMN_INDEXES[startColumnIndex + 2] + row + ':' + COLUMN_INDEXES[startColumnIndex + 1 + duration.length] + row;
            range = sheet.getRange(index);
            range.values = [value];
            range.numberFormat = '# ##0';
        }

        console.log('complete render lease projection')

        // end render data
        return context.sync();
    });
}