import * as React from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCalculator, faSpinner } from '@fortawesome/free-solid-svg-icons';
import "./app.css";

import {
  TEMPLATE_NEW_RENTAL_DISCOUNT,
  TEMPLATE_NEW_STEPPED_RENT
} from '../templates/newColumns';
import {
  HEADER_INDEXATION,
  HEADER_NEXT_INDEXATION,
  HEADER_CURRENCY,
  HEADER_LEASE_START,
  HEADER_LEASE_EXPIRY,
  HEADER_RENT_INCOME_MONTHLY,
  HEADER_ESTIMATED_VOID_PERIOD_MONTHS,
  HEADER_ERV_FOR_NEW_LEASE_MONTH,
  HEADER_NEW_LEASE_TERM_MONTHS,
  HEADER_ESTIMATED_INDEFINITE_PERIOD_END,
  HEADER_GROSS_LETTABLE_AREA_SQM,
  HEADER_TENANT_NAME,
  HEADER_TYPE_OF_AREA,
  HEADER_BREAK_OPTION_DATE,
  HEADER_AGENT_FEE_OF_HEADLINE_YEARLY_RENT,
  HEADER_LEGAL_FEE_PER_LEASE,
  HEADER_ERV_CURRENCY
} from '../templates/headerTitles';

import {
  REPORT_SHEET_NAME,
  COLUMN_INDEXES,
  DATA_SHEET_NAME,
  DATA_TABLE_NAME,

  START_INDEX_KPI_TABLE,
  START_INDEX_GLA_TABLE,
  START_INDEX_REPORT_TABLE,

  START_INDEX_REPORT_RANGE_TABLE,
  START_INDEX_CURRENCY_CONVERSION,
  START_INDEX_INDEXATION_TABLE,
  START_INDEX_ADJUSTMENT_TABLE,
  START_INDEX_DEFAULT_CURRENCY,
  ADJUSTMENT_SHEET_NAME,
} from '../constants';

import { getEndDateOfMonth, checkMonthIsInLeaseProjection, getExchangeRates, excelDateToJSDate, getMonthDiff } from '../utils';
import { FILL_AZURE_LIGHT, FILL_YELLOW_LIGHT, FILL_GRAY_LIGHT, BORDER_AZURE, FONT_YELLOW, BORDER_LIGHT_GRAY, FONT_GRAY, FONT_BLACK } from '../templates/colors';

import {
  getCurrenciesFromTableData,
  getLeaseProjectionDuration,
  getLeaseProjectionData,
} from '../lib/projection';

import { getAdjustmentData } from "../lib/adjustment";

import { renderLeaseProjectionTable } from '../render/renderLeaseProjectionTable';
import { renderGLA_Table } from '../render/renderGlaTable';
import { formatRangeBorder } from '../render/formatRangeBorder';
import { renderKPI_Table } from '../render/renderKpiTable';
import { renderChart } from '../render/renderChart';
import { renderDefaultCurrencyTable } from '../render/renderDefaultCurrencyTable';
import { renderReportRangeTable } from '../render/renderReportRangeTable';
import { renderIndexationTable } from '../render/renderIndexationTable';
import { renderCurrencyConversionTable } from '../render/renderCurrencyConversionTable';
import { renderAdjustmentTable } from '../render/renderAdjustmentTable';
import { renderLegendTable } from '../render/renderLegendTable';

import {
  arrangeTableData,
} from '../lib/common';

import { COLUMN_COUNT } from '../constants';
const MAX_COLUMN_COUNT = 100;

export default class Calculation extends React.Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
      isCalculating: false,
      currencyUnitsCount: 0,
    };
  }

  /**
   * Calculation Event Dispatcher
   */
  _createReport = async () => {
    const _self = this;
    this.setState({ isCalculating: true })
    try {
      await Excel.run(async context => {
        var sheets = context.workbook.worksheets;
        var currentSheets = sheets.load("items");

        var dataSheet = context.workbook.worksheets.getItem(DATA_SHEET_NAME);
        var dataTable = dataSheet.tables.getItem(DATA_TABLE_NAME);

        // Get data from the header row
        var dataHeaderRange = dataTable.getHeaderRowRange().load("values");
        // Get data from the table
        var dataBodyRange = dataTable.getDataBodyRange().load("values");
        var dataFormatRange = dataTable.getDataBodyRange().load("numberFormat");

        return context.sync()
          .then(async () => {

            let renderTables = async () => {
              let duration = getLeaseProjectionDuration(tableData);

              let { currencyUnitsCount, exchangeData } = await renderCurrencyConversionTable(tableData);
              this.setState({
                currencyUnitsCount,
                exchangeData,
              })
              renderReportRangeTable(duration);
              renderDefaultCurrencyTable();
              renderIndexationTable();

              let leaseProjectionData = getLeaseProjectionData(tableData, duration, exchangeData);

              renderLeaseProjectionTable(leaseProjectionData, duration)
              let occupancies = await renderGLA_Table(leaseProjectionData, duration);
              await renderKPI_Table(leaseProjectionData, occupancies, duration);
              renderChart(duration);
              renderLegendTable();
              renderAdjustmentTable(leaseProjectionData, duration)

              _self.setState({ isCalculating: false });
            }

            var exist = false;
            let tableData = arrangeTableData(dataHeaderRange.values[0], dataBodyRange.values, dataFormatRange.numberFormat[0]);
            currentSheets.items.forEach(elem => {
              if (elem.name == REPORT_SHEET_NAME) {
                exist = true;
              }
            })

            if (exist) {
              console.log('recalculate');
              renderTables();
            } else {
              console.log('create report');
              var sheet = sheets.add(REPORT_SHEET_NAME);
              sheet.load("name, position");
              return context.sync()
                .then(async () => {
                  var sheet = context.workbook.worksheets.getItem(REPORT_SHEET_NAME);
                  sheet.activate();
                  sheet.load("name");
                  renderTables();
                });
            }
            return context.sync();
          });
      });
    } catch (error) {
      console.error(error);
    }
  }

  render() {
    const { isOfficeInitialized } = this.props;
    const { isCalculating } = this.state;

    if (!isOfficeInitialized) {
      return (
        <div></div>
      );
    }

    return (
      <div className="section">
        <p className="section-title">Create Report</p>
        <div className="button-container">
          {
            isCalculating ? (
              <div className="button">
                <FontAwesomeIcon className="icon" icon={faSpinner} spin />
              </div>
            ) :
              (
                <div className="button" onClick={this._createReport}>
                  <FontAwesomeIcon className="icon" icon={faCalculator} />
                  <span>New Projection Report</span>
                </div>
              )
          }
        </div>
      </div>
    );
  }
}
